@import url(https://fonts.googleapis.com/css?family=Walter+Turncoat);
body {
  font-family: 'Walter Turncoat', cursive;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

footer, header, nav, container {
  display: block;
}
.navbar {
    background: #FED90F;
    min-height: 70px;
    position: fixed;
    width: 100%;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: #fff;
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);
    z-index: 99;
    white-space: nowrap;
    text-shadow: 0px 1px 8px #B4BABD;
}

.navbar ul {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
}

.navbar li {
    display: inline-block;
    padding: 26px 0;
    width: 33.3%;
    text-align: center;
}

.headliner {
    text-align: center;
    margin-left: 15%;
    color: #fff
}

.navbar a {
    text-decoration: none;
    color: #fff;
    font-weight: 900;
}
.card {
  background-size: cover;
    height: 170px;
    width: 170px;
    margin: 8px 25px;
    border: 5px solid #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
    cursor: pointer;
    -webkit-transition: -webkit-transform .1s;
    transition: -webkit-transform .1s;
    transition: transform .1s;
    transition: transform .1s, -webkit-transform .1s; /* Animation */
    text-align: center
}

.card > .img-container > img {
  max-height: 170px;
    max-width: 170px;
    -webkit-transition: -webkit-transform .8s;
    transition: -webkit-transform .8s;
    transition: transform .8s;
    transition: transform .8s, -webkit-transform .8s; /* Animation */
}

.card:hover {
  -webkit-transform: scale(1.06);
          transform: scale(1.06); /* (106% zoom */
  box-shadow: 0 9px 15px rgba(0,0,0,.16), 0 9px 15px rgba(0,0,0,.20);
}

.card > .img-container > img:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02); /* (106% zoom */
}
.container {
  display: flex;
  min-height: 80%;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
  width: 90%;
  margin: 0 auto;
}

@media (min-width: 1100px) {
  .container {
    padding-left: 100px;
    padding-right: 100px;
    width: 1100px;
  }
}

.header {
    background-color: #70D1FE;
    background-image: url(/ClickyGame/static/media/clouds2.86a184d1.jpg);
    background-repeat: repeat;
    color: white;
    min-height: 175;
    padding: 180px 20px;
    padding-bottom: 140px;
    text-align: center;
}
  
.header h1 {
    margin: 0;
    margin-bottom: 30px;
    font-size: 6rem;
    text-shadow: 0px 1px 8px #B4BABD;
}

  .header h2 {
    text-align: center;
    color: white;
    font-size: 2rem;
    display: block;
    -webkit-margin-before: 0.83em;
            margin-block-start: 0.83em;
    -webkit-margin-after: 0.83em;
            margin-block-end: 0.83em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    font-weight: bold;
    text-shadow: 0px 1px 8px #B4BABD;
  }
.footer {
    background: #D1B271;
    height: 100px;
    position: relative;
    color: #fff;
    overflow: auto;
}

.footer>.bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #FED90F;
    padding: 8px;
    font-weight: 600;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

